import { Location } from '@angular/common';
import { Component, OnInit } from '@angular/core';
import { FormControl } from '@angular/forms';
import { Router } from '@angular/router';

import { ToastController } from '@ionic/angular';
import { ApiService } from 'src/app/services/api.service';

@Component({
  selector: 'app-feedback',
  templateUrl: './feedback.page.html',
  styleUrls: ['./feedback.page.scss'],
})
export class FeedbackPage implements OnInit {
  rating = new FormControl();
  message = new FormControl();
  routerData: any;

  constructor(private router: Router, private location: Location, private api: ApiService, public toastCtrl: ToastController,) { }

  ngOnInit() {
    if (this.router.getCurrentNavigation() != null) {
      this.routerData = this.router.getCurrentNavigation().extras.state;
    }
  }

  onSubmit() {
    const feedback = {
      customerId: localStorage.getItem('customer_id') || '',
      feedback: this.rating.value,
      suggestion: this.message.value
    }
    // console.log(feedback);
    this.api.storeFeedbackInfo(feedback).subscribe(resp => {
      console.log(resp);
      this.onSkip();
    },err => {
        console.log('ERROR: ', err)
        this.openToast('unable to save feedback')
    })
  }

  onSkip() {
    if(this.routerData?.goBack) {
      this.location.back();
    } else {
      // NO NEED TO CLEAR THE SESSION AS PER THE DISCUSSION
      // localStorage.clear();
      // sessionStorage.clear();
      this.router.navigate(['login-landing'], { replaceUrl: true });
    }
  }

  previous1() {
    this.router.navigate(['profile']);
  }

  async openToast(message) {
    const toast = await this.toastCtrl.create({
      message: `${message}`,
      duration: 2500,
      position: 'bottom',
    });
    toast.present();
  }
}
